	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"That Was Fast","date":"19 Jun 2002","slug":"/2002-06-19-that-was-fast/","tags":"Y2002"},"html":"<p>Just 24 hours after a<a href=\"http://httpd.apache.org/info/security_bulletin_20020617.txt\">security advisory</a> for Apache was publicized, new versions 1.3.26 and 2.0.39 with a fix are available. Most Apache updates are nice-to-have, but this one is a got-to-have.</p>"},"next":{"fields":{"title":"Bury the Hatchet","date":"19 Jun 2002","slug":"/2002-06-19-bury-the-hatchet/"}},"previous":{"fields":{"title":"Version Fatigue","date":"20 Jun 2002","slug":"/2002-06-20-version-fatigue/"}}},{"node":{"fields":{"title":"Bury the Hatchet","date":"19 Jun 2002","slug":"/2002-06-19-bury-the-hatchet/","tags":"Y2002"},"html":"<p><a href=\"http://www.schockwellenreiter.de/2002/06/19.html#a6154\">Das Kriegsbeil ist begraben</a>. Good news for German bloggers, and especially for Jörg Kantel (aka Der Schockwellenreiter). After a phone call between Jörg and a representitive of the Suhrkamp Verlag, the cease-and-dissist letter has been withdrawn, including the demand for legal fees. The Kantels are now free to spend the &euro;1200 as they wish... for example, on their dog Zebu.</p>\n<p>The issue of which links are legal in Germany and which are not is still open. There are previous cases of FTPExplorer/SelfHTML and the Hamburg ruling requiring disclaimers for outside links are not particularly comforting. But American bloggers  have similar problems, for example  National Public Radio  requiring <a href=\"http://www.vfth.com/2002/06/19#NDozMzoyNSBBTQdbdb\">permisssion</a> for links.</p>"},"next":{"fields":{"title":"World Cup Germany-USA","date":"17 Jun 2002","slug":"/2002-06-17-world-cup-germany-usa/"}},"previous":{"fields":{"title":"That Was Fast","date":"19 Jun 2002","slug":"/2002-06-19-that-was-fast/"}}},{"node":{"fields":{"title":"World Cup Germany-USA","date":"17 Jun 2002","slug":"/2002-06-17-world-cup-germany-usa/","tags":"Y2002"},"html":"<p><a href=\"http://www.papascott.de/2002/02/23\"><img alt=\"PapaScott in red, white and blue warpaint\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/06/smhmakeup-thumb.jpg\" width=\"125\" height=\"98\" border=\"0\" align=\"right\" /></a>Now that Germany and the US are meeting in the World Cup quarterfinals, my co-workers are asking if I'm going to come to work on Friday like <a href=\"http://www.papascott.de/2002/02/23\">this</a>.</p>"},"next":{"fields":{"title":"Wir sind wieder wer","date":"17 Jun 2002","slug":"/2002-06-17-wir-sind-wieder-wer/"}},"previous":{"fields":{"title":"Bury the Hatchet","date":"19 Jun 2002","slug":"/2002-06-19-bury-the-hatchet/"}}}],"pathPrefix":"","first":false,"last":false,"index":882,"pageCount":1101}}

